.news-card {
  display: block;
  position: relative;
  box-sizing: border-box;
  object-fit: contain;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  border-radius: 20px;
  width: 100%;
  /* height: 100%; */
  /* height: 576px; */
  overflow: hidden;
}

.news-card__img {
  display: block;
  width: 100%;
  height: 272px;
  object-fit: none;
  object-position: center;
}

@media screen and (max-width: 1024px) {
  .news-card__img {
    height: 20vw;
  }
}

@media screen and (max-width: 780px) {
  .news-card__img {
    height: 150px;
  }
}

.news-card__lables {
  position: absolute;
  top: 0;
  left: 0;
  margin: 24px;
  width: calc(100% - 48px);
  box-sizing: border-box;
  justify-content: space-between;
  display: flex;
  flex-direction: row;
  font-family: 'Roboto', 'Inter', Arial, sans-serif;
  font-weight: 500;
  line-height: 1.2;
  text-align: center;
}

@media screen and (max-width: 780px) {
  .news-card__lables {
    margin: 8px;
    max-height: 40px;
    width: calc(100% - 16px);
  }
}

.news-card__save-btn {
  width: 40px;
  height: 40px;
  background-color: #fff;
  background-image: url('../../images/saveDisable.svg');
  display: block;
  margin: 0 0 0 auto;
  object-fit: contain;
  background-repeat: no-repeat;
  background-size: 14px 19px;
  background-position: center;
  cursor: pointer;
  box-shadow: none;
  box-sizing: border-box;
  padding: 0;
  border: none;
  outline: none;
  border-radius: 8px;
}

/* .news-card__save-btn {
  background-image: url("../../images/saveActive.svg");
} */

.news-card__delete-btn {
  padding: 8px;
  width: 40px;
  height: 40px;
  border: none;
  outline: none;
  border-radius: 8px;
  margin: 0;
  object-fit: contain;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  box-shadow: none;
  box-sizing: border-box;
  background-color: #fff;
  background-size: 24px 24px;
  background-image: url('../../images/trash.svg');
}

.news-card__inf-container {
  padding: 20px 24px 24px 24px;
  /* height: calc(53% - 44px); */
  min-height: 50%;
  overflow: hidden;
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: space-between;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
}

@media screen and (max-width: 780px) {
  .news-card__inf-container {
    /* height: calc(53% - 32px); */
    padding: 16px;
  }
}

.news-card__content {
  /* max-height: 13.5em; */
  min-height: 194px;
}

.news-card__date {
  color: #b6bcbf;
  font-family: 'Source Sans Pro', 'Roboto', 'Inter', Arial, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.3;
  margin-bottom: 12px;
}

.news-card__title {
  color: #000;
  font-family: 'Roboto Slab', 'Inter', Arial, sans-serif;
  font-weight: 400;
  font-size: 26px;
  line-height: 1.15;
  margin: 0 0 16px;
  white-space: normal;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  min-height: 2em;
}

@media screen and (max-width: 1024px) {
  .news-card__title {
    font-size: 2.5vw;
  }
}

@media screen and (max-width: 780px) {
  .news-card__title {
    font-size: 22px;
  }
}

.news-card__text {
  color: #000;
  font-family: 'Roboto', 'Inter', Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.4;
  /* margin-bottom: 18px; */
  /* max-height: 85px; */
  white-space: normal;
  overflow: hidden;
  /* text-overflow: ellipsis; */
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  min-height: 4em;
}

.news-card__resource {
  color: #b6bcbf;
  font-family: 'Roboto Slab', 'Inter', Arial, sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  margin-top: 10px;
  margin-bottom: 0;
}

.news-card__search-tag {
  display: block;
  border-radius: 10px;
  color: #000;
  background-color: #fff;
  padding: 8px 12px;
  font-family: 'Roboto', 'Inter', Arial, sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.7;
}

.news-card__delete-notification {
  display: block;
  border-radius: 10px;
  background-color: #fff;
  padding: 13px 10px;
  font-size: 12px;
  position: absolute;
  top: 0;
  right: 45px;
  opacity: 0;
  transition: opacity 1s ease 0.1s;
}

@media screen and (max-width: 780px) {
  .news-card__delete-notification {
    display: none;
    padding: 8px 0;
    font-size: 10px;
    width: 90px;
  }
}

.news-card__delete-btn:hover {
  background-image: url('../../images/trashBlack.svg');
}

.news-card__delete-btn:hover + .news-card__delete-notification {
  opacity: 1;
}

.news-card__save-btn:hover {
  background-image: url('../../images/saveHover.svg');
}

.news-card__save-notification {
  display: none;
  border-radius: 10px;
  background-color: #fff;
  padding: 14px 10px;
  font-size: 10px;
  line-height: 1.2;
  text-align: center;
  position: absolute;
  top: 0;
  right: 45px;
  opacity: 0;
  transition: opacity 1s ease 0.1s;
}

.news-card__save-btn:hover + .news-card__save-notification {
  opacity: 1;
}

.saved {
  background-image: url('../../images/saveActive.svg');
}

.saved:hover {
  background-image: url('../../images/saveActive.svg');
}

.news-card__link-to-news {
  text-decoration: none;
}
